.container {
    display: -ms-flexbox;
    display: flex; 
    -ms-flex-align: center; 
        align-items: center; 
    -ms-flex-pack: center; 
        justify-content: center;
    min-height: 100vh;
}

.row {
    width: 900px;
    margin-bottom: 5%;
}

.content-wrapper p {
    margin: 0 0 20px 0;
}

.name {
    margin-bottom: 25px;
}

.text-link-resume {
    -webkit-text-decoration-line: underline;
            text-decoration-line: underline;
    font-weight:bold;
    color: black;
}

.text-link-resume:hover {
    -webkit-text-decoration-line: underline;
            text-decoration-line: underline;
    font-weight:bold;
    color: black;
}

.text-link-mgte {
    color: #000;
    font-weight:bold;
}

.text-link-mgte:hover {
    -webkit-text-decoration-line: underline;
            text-decoration-line: underline;
    font-weight:bold;
    color: #000;
}

.text-link-fb {
    font-weight:bold;
    color: #0668E1;
}

.text-link-fb:hover {
    -webkit-text-decoration-line: underline;
            text-decoration-line: underline;
    font-weight:bold;
    color: #0668E1;
}

.text-link-learn-more {
    font-family: 'Lato';
    font-weight: 400;
    font-size: 17px;
}

.future-text {
    margin-top: 40px;
}

.links-row a{
    font-size: 25px;
    color: rgb(202, 198, 198);
    margin: 0 15px 0 0;
}

.links-row a:hover {
    color: black;
    -webkit-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s;
}

h1 {
    font-family: 'Fira Sans';
    font-weight: 900;
    font-size: 100px;
}

h2 {
    font-family: 'Fira Sans';
    font-weight: 400;
    font-size: 60px;
}

p {
    font-family: 'Lato';
    font-weight: 400;
    font-size: 17px;
}

@media (max-width: 991px) {
    h1 {
        font-size: 70px;
    }
}

@media (max-width: 575px) {
    h1 {
        font-size: 50px;
    }

    .row {
        margin: 0 40px;
    }
}

@media (max-width: 450px) {
    h1 {
        font-size: 50px;
    }
    
    .row {
        margin: 0 10px;
    }
}